<template>
  <div class="works">
    <div class="work_container">
      <div class="filter_bar">
        <div class="work_search">
          <input type="text" v-model="filterText" placeholder="尋找" />
        </div>
        <div class="progress">
          <p>進度</p>
          <div class="switch" @click="showAll()">全部</div>
          <div class="switch" @click="showDone()">已完工</div>
          <div class="switch" @click="showIng()">進行中</div>
        </div>
        <div class="work_type">
          <div>
            <input type="checkbox" id="allgud" @click="allTrue()" v-model="displayer.all" />
            <label for="allgud">全部</label>
          </div>
          <div>
            <input
              type="checkbox"
              id="l1Open"
              @click="closeAll(),optIn01()"
              v-model="displayer.l1Open"
            />
            <label for="l1Open">辦公室規劃設計工程</label>
          </div>
          <div>
            <input
              type="checkbox"
              id="l2Open"
              @click="closeAll(),optIn02()"
              v-model="displayer.l2Open"
            />
            <label for="l2Open">廠務各式修改維修工程</label>
          </div>
          <div>
            <input
              type="checkbox"
              id="l3Open"
              @click="closeAll(),optIn03()"
              v-model="displayer.l3Open"
            />
            <label for="l3Open">專案廠房建置工程</label>
          </div>
          <div>
            <input
              type="checkbox"
              id="l4Open"
              @click="closeAll(),optIn04()"
              v-model="displayer.l4Open"
            />
            <label for="l4Open">無塵室產線各式加工件製品</label>
          </div>
        </div>
      </div>
      <div class="work_list">
        <template v-for="item in filterArray">
          <template v-if="item.finish== true || item.inprogress == true">
            <div
              class="work_item"
              v-if=" item.office == true || item.factoryFix == true || item.factoryBuild == true || item.machining == true "
              :key="item.index"
            >
              <img :src="item.img" alt />
              <p class="work_title">{{item.title}}</p>
              <div class="tag_block">
                <div class="build_type">{{item.buildType}}</div>
                <div class="tag_finish" v-if="item.finish == true">已完工</div>
                <div class="tag_progress" v-if="item.inprogress == true">進行中</div>
              </div>
            </div>
          </template>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: "",
      filterText: "",
      displayer: {
        l1Open: true,
        l2Open: true,
        l3Open: true,
        l4Open: true,
        all: true
      },
      list1: [
        {
          img: "https://arrgrr.github.io/proMask/static/work.jpg",
          title: "XXXX設計工程",
          buildType: "辦公室規劃設計工程",
          office: true,
          finish: true
        },
        {
          img: "https://arrgrr.github.io/proMask/static/work.jpg",
          title: "aaaaa設計工程",
          buildType: "廠務各式修改維修工程",
          factoryFix: true,
          inprogress: true
        },
        {
          img: "https://arrgrr.github.io/proMask/static/work.jpg",
          title: "bbbbb設計工程",
          buildType: "專案廠房建置工程",
          factoryBuild: true,
          finish: true
        },
        {
          img: "https://arrgrr.github.io/proMask/static/work.jpg",
          title: "ccccc設計工程",
          buildType: "無塵室產線各式加工件製",
          machining: true,
          finish: true
        },
        {
          img: "https://arrgrr.github.io/proMask/static/work.jpg",
          title: "asasas設計工程",
          buildType: "無塵室產線各式加工件製",
          machining: true,
          inprogress: true
        },
        {
          img: "https://arrgrr.github.io/proMask/static/work.jpg",
          title: "abcabc設計工程",
          buildType: "廠務各式修改維修工程",
          factoryBuild: true,
          finish: true
        }
      ]
    };
  },
  methods: {
    allTrue: function() {
      if (this.displayer.all == false) {
        this.displayer.l1Open = true;
        this.displayer.l2Open = true;
        this.displayer.l3Open = true;
        this.displayer.l4Open = true;
        for (var i = 0; i < this.list1.length; i++) {
          if (this.list1[i].office == "office") {
            this.list1[i].office = true;
          } else if (this.list1[i].factoryFix == "factoryFix") {
            this.list1[i].factoryFix = true;
          } else if (this.list1[i].factoryBuild == "factoryBuild") {
            this.list1[i].factoryBuild = true;
          } else if (this.list1[i].machining == "machining") {
            this.list1[i].machining = true;
          }
        }
      }
    },
    closeAll: function() {
      if (this.displayer.all == true) {
        this.displayer.all = false;
      }
    },
    optIn01: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].office == true) {
          this.list1[i].office = "office";
        } else if (this.list1[i].office == "office") {
          this.list1[i].office = true;
        }
      }
    },
    optIn02: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].factoryFix == true) {
          this.list1[i].factoryFix = "factoryFix";
        } else if (this.list1[i].factoryFix == "factoryFix") {
          this.list1[i].factoryFix = true;
        }
      }
    },
    optIn03: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].factoryBuild == true) {
          this.list1[i].factoryBuild = "factoryBuild";
        } else if (this.list1[i].factoryBuild == "factoryBuild") {
          this.list1[i].factoryBuild = true;
        }
      }
    },
    optIn04: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].machining == true) {
          this.list1[i].machining = "machining";
        } else if (this.list1[i].machining == "machining") {
          this.list1[i].machining = true;
        }
      }
    },
    showIng: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].finish == true) {
          this.list1[i].finish = "finish";
        } else if (this.list1[i].inprogress == "inprogress") {
          this.list1[i].inprogress = true;
        }
      }
    },
    showDone: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].inprogress == true) {
          this.list1[i].inprogress = "inprogress";
        } else if (this.list1[i].finish == "finish") {
          this.list1[i].finish = true;
        }
      }
    },
    showAll: function() {
      for (var i = 0; i < this.list1.length; i++) {
        if (this.list1[i].finish == "finish") {
          this.list1[i].finish = true;
        } else if (this.list1[i].inprogress == "inprogress") {
          this.list1[i].inprogress = true;
        }
      }
    }
  },
  computed: {
    filterArray: function() {
      var vm = this;
      return vm.list1.filter(function(item) {
        return item.title.match(vm.filterText);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./src/globalStyle";
.works,
.work_search,
.progress,
.work_type,
.filter_bar,
.worklist,
.work_item,
.work_container {
  display: flex;
}
.works {
  background: url(https://arrgrr.github.io/proMask/static/BG_pattern.jpg) bottom
    left no-repeat;
  justify-content: center;
  overflow: auto;
}

.work_container {
  width: 1366px;
  padding-top: $sL * 2;
}

.filter_bar {
  flex-direction: column;
  flex: 2;
  padding: 0 $sM;
}
.work_search {
  input {
    height: 40px;
    font-size: 18px;
    display: flex;
    flex: 1;
    padding: $sXs $sS;
  }
  margin-bottom: $sS;
}
.work_type {
  flex-direction: column;
  margin-top: $sM;
  div {
    display: flex;
    align-items: center;
    margin: $sXs 0;
    input {
      margin-right: $sS;
    }
  }
}

.progress {
  flex-wrap: wrap;
  flex-direction: row;
  padding-bottom: $sM;
  border-bottom: 1px solid $black20;
  p {
    flex: 1 100%;
    color: $black60;
  }
  .switch {
    flex: 1;
    padding: $sS;
    margin: 0 $sS;
    align-items: center;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    border: 1px solid $black20;
    border-radius: 4px;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}

.work_list {
  flex: 4;
  flex-wrap: wrap;
  flex-direction: row;
}

.work_item {
  display: inline-flex;
  flex-direction: column;
  flex: 1 50%;
  width: 400px;
  background: #ffffff;
  margin: 0 $sMs $sMs;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  .work_title {
    font-size: 24px;
    padding: 0;
    margin: $sS $sMs;

    color: $black80;
  }
}

.tag_block {
  justify-content: space-around;
  padding: 0 $sMs $sMs;
}

.build_type,
.tag_progress,
.tag_finish {
  display: inline-flex;
  padding: 4px $sS;
  border-radius: 4px;
  font-size: 16px;
}
.tag_progress {
  background: #ff7a00;
  color: $white;
}

.tag_finish {
  background: #a1cf1c;
  color: $white;
}

.build_type {
  background: rgba($black, 0.1);
  color: rgba($black, 0.6);
}
</style>




