import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from '@/components/page/home';
import About from '@/components/page/about';
import Services from '@/components/page/services';
import Works from '@/components/page/works';
import Contact from '@/components/page/contact';
import Header from '@/components/page/header'
import Footer from '@/components/page/footer';
import Service01 from '@/components/page/service01';
import Service02 from '@/components/page/service02';
import Service03 from '@/components/page/service03';
import Service04 from '@/components/page/service04';
import Search from '@/components/page/search';

Vue.use(VueRouter);
Vue.component("modal", {
  template: "#modal-template"
});

export default new VueRouter({
  routes: [{
      name: 'modal',
      path: '/search',
      components: {
        default: Search,
        header: Header,
        footer: Footer,
      }
    }, {
      name: 'Home',
      path: '/index',
      components: {
        default: Home,
        header: Header,
        footer: Footer,
      }
    },
    {
      name: 'About',
      path: '/about',
      components: {
        default: About,
        header: Header,
        footer: Footer,
      },

    },
    {
      name: '',
      path: '/services',
      components: {
        default: Services,
        header: Header,
        footer: Footer,
      },
      children: [{
        name: '服務1',
        path: '',
        component: Service01,
      }, {
        name: '服務2',
        path: 'service02',
        component: Service02,
      }, {
        name: '服務3',
        path: 'service03',
        component: Service03,
      }, {
        name: '服務4',
        path: 'service04',
        component: Service04,
      }, ]


    },
    {
      name: 'Works',
      path: '/works',
      components: {
        default: Works,
        header: Header,
        footer: Footer,
      },

    },
    {
      name: 'Contact',
      path: '/contact',
      components: {
        default: Contact,
        header: Header,
        footer: Footer,
      },

    },
  ]
})



