<template>
  <div class="service">
    <div class="service_container">
      <div class="menu_sec">
        <h3>我們的服務</h3>
        <router-link @click.native="active_item" class="menu" to="/services/">
          <span></span> 辦公室規劃設計工程
        </router-link>
        <router-link @click.native="active_item" class="menu" to="/services/service02">
          <span></span>專案廠房建置工程
        </router-link>
        <router-link @click.native="active_item" class="menu" to="/services/service03">
          <span></span>廠務各式修改維修工程
        </router-link>
        <router-link @click.native="active_item" class="menu" to="/services/service04">
          <span></span>無塵室產線各式加工件製品
        </router-link>
      </div>
      <router-view class="content"></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: true
    };
  },
  methods: {
    active_item: function(e) {
      var menu_list = document.getElementsByClassName("menu");
      for (var i = 0; i < menu_list.length; i++) {
        menu_list[i].classList.remove("menu_act");
      }
      e.target.classList.add("menu_act");
      console.log(e.target.classList);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./src/globalStyle";
.service_container,
.content,
.menu_sec,
.menu {
  display: flex;
}

.service {
  background: url(https://arrgrr.github.io/proMask/static//BG_pattern.jpg)
    bottom left no-repeat;
  justify-content: center;
}

.service_container {
  width: 1080px;
  padding-top: $sL;
}
.menu_sec {
  flex: 2;
  flex-direction: column;
  padding-top: $sL;
}
.content {
  flex: 4;
  flex-direction: column;
}
h3 {
  font-size: 24px;
  color: $black80;
}

.menu {
  text-decoration: none;
  color: $black60;
  font-size: 20px;
  font-weight: 200;
  padding: $sS 0;
  transition: 200ms;
  span {
    display: flex;
    box-sizing: border-box;
    width: $sMs;
    height: $sMs;
    border: 1px solid $black20;
    border-radius: 100px;
    align-self: center;
    margin-right: $sS;
    transition: 200ms;
  }
  &:hover {
    font-weight: 400;
    color: $black;
    span {
      background: $fire;
      border: 0;
    }
  }
}
.menu_act {
  font-weight: 400;
  color: $black;
  span {
    background: $fire;
    border: 0;
  }
}
</style>





